body {
  width: 100vw;
  height: 100vh;
}

.container {
  width: 100%;
  height: 100vh;
  background-image: url(/currency-converter-react/static/media/background.171747fe.jpg);
  background-position: center;
  background-size: auto;
  background-repeat: no-repeat;
}

.app {
  max-width: 500px;
  min-height: 500px;
  margin: 0px auto;
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
  background-color: #fff;
  border-radius: 25px;
  padding: 10px;
  box-shadow: 0 0 25px 25px rgba(0, 0, 0, 0.6);
}

.header {
  font-size: 26px;
  text-align: center;
  margin: 10px 0;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  font-weight: bold;
}

.logo {
  display: -webkit-flex;
  display: flex;
}

.logo img {
  width: 150px;
  height: 150px;
  margin: 15px auto;
  border-radius: 30%;
}

/* forms styling */
.forms {
  margin-bottom: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: 'Open Sans', sans-serif;
}

.forms div {
  margin-bottom: 10px;
  text-transform: uppercase;
}

.forms .error-message {
  color: #ff0000;
}

.amount-input {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10px;
}

.amount-input input {
  width: 70%;
  height: 35px;
  border: 2px solid #000;
  border-radius: 15px;
  font-size: 20px;
  padding: 0 15px 0 15px;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  transition: 1s;
}

.amount-input select {
  box-sizing: content-box;
  width: 15%;
  height: 35px;
  border: 2px solid #000;
  border-radius: 15px;
  font-size: 20px;
  margin-left: 10px;
  font-family: 'Open Sans', sans-serif;
  transition: 1s;
}

.amount-input button {
  box-sizing: content-box;
  width: 100%;
  height: 35px;
  border: 2px solid #000;
  border-radius: 15px;
  font-size: 20px;
  font-family: 'Open Sans', sans-serif;
  margin: 20px 10px 0px 10px;
  text-transform: uppercase;
  transition: 1s;
}

.amount-input input:focus,
.amount-input select:focus {
  border: 2px solid rgba(80, 171, 212, 0.808);
}

.amount-input button:hover {
  background-color: rgb(39, 176, 218);
}

.output {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 20px;
  padding: 20px;
  height: 150px;
  max-height: 200px;
  background-color: rgba(179, 233, 249, 0.671);
  border-radius: 15px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
}

.output .rate {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
}

.output .exchangeResult {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  font-size: 25px;
  font-weight: bold;
}

.output p {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  font-size: 35px;
  font-weight: bold;
}

@media screen and (max-width: 420px) {
  .app {
    height: 80vh;
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }

  .amount-input input {
    width: 60%;
    font-size: 15px;
  }

  .amount-input select {
    width: 20%;
  }
}

@media screen and (max-width: 380px) {
  .app {
    height: 85vh;
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }

  .amount-input input {
    width: 60%;
    font-size: 15px;
  }

  .amount-input select {
    width: 20%;
  }

  .output {
    height: 100px;
  }

  .output .exchangeResult {
    font-size: 20px;
    font-weight: bold;
  }

  .output p {
    font-size: 25px;
    font-weight: bold;
  }
}

@media screen and (max-width: 320px) {
  .app {
    height: 100vh;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }

  .amount-input input {
    width: 60%;
    font-size: 15px;
  }

  .amount-input select {
    width: 20%;
    font-size: 15px;
  }

  .output {
    height: 120px;
  }

  .output .exchangeResult {
    font-size: 20px;
    font-weight: bold;
  }

  .output p {
    font-size: 25px;
    font-weight: bold;
  }
}

@media screen and (max-width: 280px) {
  .amount-input select {
    width: 20%;
    font-size: 10px;
  }
}

@media screen and (orientation: landscape) and (max-width: 640px) {
  .app {
    min-height: 200px;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    padding-bottom: 5px;
  }

  .header {
    font-size: 20px;
    text-align: center;
    margin: 5px 0;
  }

  .logo img {
    width: 100px;
    height: 100px;
    margin: 5px auto;
    border-radius: 30%;
  }

  .amount-input {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: no-wrap;
            flex-wrap: no-wrap;
  }

  .amount-input input {
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    font-size: 12px;
  }

  .amount-input select {
    box-sizing: content-box;
    -webkit-flex-basis: 15%;
            flex-basis: 15%;
    height: 35px;
    margin-left: 0px;
  }

  .amount-input button {
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
    margin-top: 0px;
  }

  .output {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 5px;
    padding: 5px;
    height: 90px;
  }

  .output .rate {
    font-size: 11px;
  }

  .output .exchangeResult {
    font-size: 15px;
  }

  .output p {
    font-size: 25px;
  }
}

